import React , {useState , useEffect} from "react";
import Trades from "../../components/Trade/Trades";
import AddTrade from "../../components/Trade/AddTrade";
import './index.css';

const Trade:React.FC = ()=>{

    const [Show,setShow] = useState<boolean>(true);

    return(
        <div className="ControlTrade">
           <div className="TradeRoute">
            <span 
            style={{fontSize:'20px',
            cursor:'pointer' ,
            borderBottom: Show ? '1px solid #348dfe' : ''}}
            onClick={()=>setShow(!Show)}
            >现存商品</span>
            <span 
            style={{fontSize:'20px',
            cursor:'pointer' ,
            borderBottom: !Show ? '1px solid #348dfe' : ''}}
            onClick={()=>setShow(!Show)}
            >添加商品</span>
           </div>
           <div className="TradeView">
           <Trades isShow={Show}/>
           <AddTrade isShow={Show}/>
           </div>
        </div>
    )
}

export default Trade;