import React , {useState , useRef}from "react";
import axios from 'axios';
import { Button, Input , InputRef, Upload } from "antd";
import { v4 as uuidv4 } from 'uuid';
import {  PlusOutlined } from '@ant-design/icons';
import TravelNetWork from "../../travelnetwork";

const AddTravel:React.FC = ()=>{


    const [fileList, setFileList] = useState<any>([]);
    const [Cover,setCover] = useState<any>([]);
    const TradeName = useRef<InputRef>(null);
    const TradePrice = useRef<InputRef>(null);
    const BussinessPhone = useRef<InputRef>(null);
    const BussinessEmail = useRef<InputRef>(null);
    const StarLevel = useRef<InputRef>(null);
    const openHours = useRef<InputRef>(null);
    const TradeIntroduction = useRef<HTMLTextAreaElement>(null);
    const href = new TravelNetWork();
    const uploadButton = (
        <div>
            <PlusOutlined />
            <div style={{ marginTop: 8 }}>Upload</div>
        </div>
    );


    const handleChange = (info:any) => {
        setFileList(info.fileList)
    }

    const CoverChange = (info:any)=>{
        setCover(info.fileList);
      }

    const Submit=()=>{
        let formdata = new FormData();
        formdata.append('TravelName',String(TradeName.current?.input?.value));
        formdata.append('Rates',String(TradePrice.current?.input?.value));
        formdata.append('startlevel',String(StarLevel.current?.input?.value));
        formdata.append('BussinessPhone',String(BussinessPhone.current?.input?.value));
        formdata.append('BussinessEmail',String(BussinessEmail.current?.input?.value));
        formdata.append('text',String(TradeIntroduction.current?.value));
        formdata.append('images',JSON.stringify(fileList));
        formdata.append('OpenHours',String(openHours.current?.input?.value));
        formdata.append('cover',JSON.stringify(Cover));
        formdata.append('uuid',uuidv4());
        axios.post(href.publish,formdata,{
            headers: { "content-type": "multipart/form-data" }
        }).then((res)=>{
            if(res.data.status == true){
                alert('添加成功!');
            }
            else{
                alert('当前网络繁忙,请稍后再试!');
            }
        }).catch((err)=>{
            throw err;
        }).finally(()=>{
           
        })
    }

    return(
        <div >
            
             <span className="TradeTitle">景点名:</span><Input ref={TradeName} style={{width:'190px',marginRight:'15px'}}/>
             <span className="TradeTitle">门票价格(元):</span><Input ref={TradePrice} style={{width:'70px',marginRight:'15px'}}/> 
             <span className="TradeTitle">景区星级:</span><Input ref={StarLevel} style={{width:'70px',marginRight:'15px'}}/> 
             <span className="TradeTitle">开放时间:</span><Input ref={openHours} style={{width:'180px',marginRight:'15px'}}/> <br/><br/>
             <span className="TradeTitle">管理员电话:</span><Input ref={BussinessPhone} style={{width:'120px',marginRight:'15px'}}/> 
             <span className="TradeTitle">管理员邮箱:</span><Input ref={BussinessEmail} style={{width:'160px'}}/>
             <div style={{marginTop:'50px'}}>
             <span className="TradeTitleB">景区封面:</span>
             <Upload
                    multiple={true}
                    listType="picture-card"
                    beforeUpload={() => {
                        return false;
                    }}
                    onChange={(info) => { CoverChange(info) }}
                >
                    {Cover.length >= 1 ? null : uploadButton}
                </Upload>
             <span className="TradeTitleB" >商品简介:</span>
             <textarea ref={TradeIntroduction} style={{resize:'none',width:'500px',outline:'none',border:'1px solid #348dfe'}} cols={10} rows={10}></textarea>
             <span className="TradeTitleB">商品图片:</span>
             <Upload
                    multiple={true}
                    listType="picture-card"
                    beforeUpload={() => {
                        return false;
                    }}
                    onChange={(info) => { handleChange(info) }}
                >
                    {fileList.length >= 6 ? null : uploadButton}
                </Upload>
             </div>
             <Button style={{
                 position: 'absolute',
                 right:'10px',
                 bottom:'50px'
             }}
             onClick={()=>Submit()}
             >点击添加</Button>
        </div>
    )
}

export default AddTravel;