import React , {useEffect , useState} from "react";
import { useLocation } from "react-router";
import axios from "axios";
import { Image } from "antd";
import Publicnetwork from '../../Publicnetwork';
import './index.css';
const UserArticle:React.FC = ()=>{
   const Location:any = useLocation();
   const {coverhref,telephone,title,uuid} = Location.state;
   const href = new Publicnetwork();

   const [imagesUrl,setImagesUrl] = useState<string[]>([]);
   const [txt,setTxt]= useState<string>('');
   const [Address,setAddress] = useState<string>('');
   const [Email,setEmail] = useState<string>('');
   const [Name,setName] = useState<string>('');

   useEffect(()=>{
      axios({
        method:'GET',
        url:href.getUserMessage,
        params:{
            telephone
        }
      }).then((res)=>{
         const {userAddress,userEmail,userName} = res.data[0];
         setAddress(userAddress);
         setEmail(userEmail);
         setName(userName);
      }).catch((err)=>{
        throw err;
      }).finally(()=>{

      })
   },[])

   useEffect(()=>{
      axios({
        method:'GET',
        url:href.getArticle,
        params:{
            uuid
        }
      }).then((res)=>{
        const {imagesurl,text} = res.data[0];
        setTxt(text);
        let result = imagesurl.split(',');
        setImagesUrl(result);
      }).catch((err)=>{
        throw err;
      }).finally(()=>{

      })
   },[])

   return(
    <>
   <div className="ArticlePages">
             <div className="ArticleTitle">
               标题: {title}
             </div>
             <div className="ArticleUserMessage">
             <span>来自: {Name}</span>
            <div><span className='UserLocation'></span> <span>{Address}</span></div>
           
             <div className="WithUserMessage">
             <span>联系作者 :</span>
             <span>QQ-Email: {Email}</span>
             <span>Telephone: {telephone}</span>
             </div>
           
             
             </div>
             <span style={{
                fontSize:'22px',
                fontWeight:'700',
                marginTop:'10px',
                marginBottom:'20px'
             }}>文章封面</span>
             <Image src={coverhref} style={
                {
                    display:'block',
                    width:'300px',
                    height:'300px'
                }
             }></Image>
             <span style={{
                fontSize:'22px',
                fontWeight:'700',
                marginTop:'40px',
                marginBottom:'20px'
             }}>文章内容</span>
             <div className="AtricleTxt">{txt}</div>
             <span style={
                {
                    fontSize:'22px',
                    fontWeight:'700',
                    marginTop:'40px',
                    marginBottom:'20px'
                 }
             }>美图欣赏</span>
             <div className="ArticleImages">
                 {
                   imagesUrl.map((item,index)=>{
                    return <Image src={item} key={item} style={{
                        display:'block',
                        width:'400px',
                        height:'400px',
                        marginTop:'20px'
                    }}/>
                   })
                 }
             </div>
         </div>
    </>
   )
}

export default UserArticle;