import React , {useEffect , useState} from "react";
import { useLocation } from "react-router";
import { Image } from "antd";
import axios from "axios";
import './index.css';
import Publicnetwork from "../../Publicnetwork";
const ArticlePages:React.FC = ()=>{
    const Location:any = useLocation();
    const href = new Publicnetwork();
    const {uuid,title,coverhref,type} = Location.state;
    const [imagesUrl,setImagesUrl] = useState<string[]>([]);
    const [txt,setText] = useState<string>();
    useEffect(()=>{
       axios({
        method:'GET',
        url:href.publicArticle,
        params:{
            'uuid':uuid,
            'type':type
        }
       }).then((res)=>{
        console.log(res.data);
        const {text,imagesurl} = res.data[0];
        setText(text);
        let imagesarr = imagesurl.split(',');
        console.log(imagesarr)
        setImagesUrl(imagesarr);
       }).catch((err)=>{
        throw err;
       }).finally(()=>{
          
       })
    },[uuid])
      
    return(
        <>
         <div className="ArticlePages">
             <div className="ArticleTitle">
               标题: {title}
             </div>
             <span style={{
                marginTop:'10px'
             }}>来自:官方发布</span>
             <Image src={coverhref} style={
                {
                    display:'block',
                    width:'300px',
                    height:'300px'
                }
             }></Image>
             <div className="AtricleTxt">{txt}</div>
             <div className="ArticleImages">
                 {
                   imagesUrl.map((item,index)=>{
                    return <Image src={item} style={{
                        display:'block',
                        width:'400px',
                        height:'400px'
                    }}/>
                   })
                 }
             </div>
         </div>
        </>
    )
}

export default ArticlePages;