import React from "react";
import PublicArticleList from "../../components/publicArticle/PublicArticleList";
import Publicnetwork from "../../Publicnetwork";
const THdashi = ()=>{

  const network = new Publicnetwork();

    return(
        <>
          <div className="pages">
             <div className="pagesmessage">
                <PublicArticleList href={network.newshref}/>
             </div>
          </div>
        </>
    )
}


export default THdashi;