import React, { useState, useEffect } from "react";
import { Image, Button, Descriptions , Empty} from "antd";
import axios from "axios";
import { v4 as uuidv4 } from 'uuid';
import networks from "../../network";
import Login from "../../components/Login/Login";
import Amend from "./amend";
import UserWrite from "./UserWrite";
import UserArticleList from "../../components/UserArticle/UserArticleList";
import './user.css';
const User: React.FC = () => {

   const [isShow, setIsShow] = useState<boolean>(false)
   const [LoginStaus,setLoginStatus] = useState<boolean>(false);
   const [userName, setUserName] = useState<string>('');
   const [Email, setEmail] = useState<string>('');
   const [Telephone, setTelephone] = useState<string>('');
   const [Address, setAdress] = useState<string>('');
   const [amendIsShow,setAmendIsShow] = useState<boolean>(false);
   const [writeShow,setWriteShow] = useState<boolean>(false);
   const [ListId,setListId] = useState<string>('a');

   const network = new networks();
   const setShow = () => {
      setIsShow(!isShow);
   }


   const fLogin = (telephone: string) => {
      axios({
         method: 'GET',
         url: network.userLogin,
         params: {
            telephone
         }
      }).then((res) => {
         const {userTelephone,userEmail,userName,userAddress} = res.data[0];
         setEmail(userEmail);
         setTelephone(userTelephone);
         setUserName(userName);
         setAdress(userAddress);
         setLoginStatus(true);
      }).catch((err) => {
         throw err;
      }).finally(() => {
         setIsShow(!isShow);
      })

   }

   const fRegister = (telephone: string, email: string) => {
      
      axios({
         method: 'GET',
         url: network.userRegister,
         params: {
            telephone,
            email
         }
      }).then((res) => {
         console.log(res.data);
         setLoginStatus(true);
      }).catch((err) => {
         throw err;
      }).finally(() => {
         setIsShow(!isShow);
      })

   }


   const AmendShow=()=>{
      setAmendIsShow(!amendIsShow);
   }

   const AmendSbumit=(name:string,email:string,address:string)=>{
     
     if(name == '') name = userName;
     if(email == '') email = Email;
     if(address == '') address = Address;
     axios({
      method:'GET',
      url:network.userAmend,
      params:{
         name,
         email,
         address,
         Telephone
      }
     }).then((res)=>{
         const {status} = res.data;
         if(status == true){
           setUserName(name);
           setEmail(email);
           setAdress(address);
           setAmendIsShow(!amendIsShow);
           alert('恭喜您操作成功!');
         }
         else{
            alert('当前网络繁忙,请稍后再试!');
         }
     }).catch((err)=>{
         throw err;
     }).finally(()=>{

     })
   }

   const changeWriteShow=()=>{
      setWriteShow(!writeShow);
   }

   const ChangeListId=()=>{
      setListId(uuidv4())
   }

   return (
      <>
         <div className="User">
            <div className="User_header">
               <Image
                  width={150}
                  src="https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png" />
               <div className="User_information">
                  <Descriptions title="User Info">
                     <Descriptions.Item label="UserName">{userName}</Descriptions.Item>
                     <Descriptions.Item label="Email">{Email}</Descriptions.Item>
                     <Descriptions.Item label="Telephone">{Telephone}</Descriptions.Item>
                     <Descriptions.Item label="Address">
                        {Address}
                     </Descriptions.Item>
                  </Descriptions>
               </div>
               <button onClick={()=>AmendShow()} style={{display:LoginStaus? 'block' : 'none'}} className="user_ChangeMessage"></button>
               <Button onClick={(e) => setIsShow(!isShow)} style={{display:LoginStaus ? 'none' : 'block'}}>登录/注册</Button>
               <Button style={{display:LoginStaus? 'block' : 'none'}} onClick={()=>changeWriteShow()} >写文章</Button>
            </div>
            <div className="User_Publish">
               <span style={{ fontSize: '25px', fontWeight: 700,display:'block',marginBottom:'10px' }}>我的发表</span>
               {
                  !LoginStaus ? <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} /> : <UserArticleList telephone={Telephone} key={ListId} href={network.userGetArticles}/>
               }
            </div>
            <Login IsShow={isShow} setShow={setShow} FLogin={fLogin} FRegister={fRegister} />
             <Amend changeShow={AmendShow} submit={AmendSbumit} isShow={amendIsShow} UserName={userName} Email={Email} Address={Address} key={Telephone}/>
             <UserWrite changeListId={ChangeListId} telephone={Telephone} changeShow={changeWriteShow} isShow={writeShow}/>
         </div>
      </>
   )
}


export default User;