import { Button } from "antd";
import { useNavigate } from "react-router";
import React from "react";

interface Iprops{
   message:{
    telephone:string,
    title:string,
    coverhref:string,
    uuid:string
   }
}

const UserArticleItem:React.FC<Iprops> = (props:Iprops)=>{
    const Navigate = useNavigate();
    const goto = ()=>{
      Navigate('/userArticle',{state:props.message,replace:true})
    }
    const {telephone,title,coverhref,uuid} = props.message;
    return(
        <>
        <li className="UserArticleItem" onClick={()=>goto()}>
          <img src={coverhref} style={{width:'80px',height:'80px'}}></img>
          <span style={{fontSize:'20px'}}>标题: {title}</span>
        </li>
        </>
    )
}

export default UserArticleItem;