import React, { useEffect, useState } from 'react';
import { Layout, Menu } from 'antd';
import { NavLink , Routes , Route } from 'react-router-dom';
import WriteArticle from '../../components/WriteArticle/writeArticle';
import ArticleList from '../../components/List/articleList';
import Trade from '../Trade';
import ControlTravel from '../../components/controltravel';
import netWorks from '../../network';
const { Content,Sider } = Layout;

const Manager: React.FC = () => {
    const [controlType,setControlType] = useState<string>('天汉大事');

    const href = new netWorks().getArticleList;

    const ChangeType = (type:string)=>{
        setControlType(type);
    }


return (
    
        <Layout hasSider>
            <Sider
                style={{
                    overflow: 'auto',
                    height: '100vh',
                    position: 'fixed',
                    left: 0,
                    top: 0,
                    bottom: 0,
                }}
            >
                <div className='logo'></div>
                <Menu style={{
                    margin: '64px 16px 0'
                }}
                    theme="dark"
                >
                    <Menu.Item key={'发表文章'}><NavLink to={'WriteArticle'}>发表文章</NavLink></Menu.Item>
                    <Menu.Item onClick={()=>ChangeType('天汉大事')} key={'天汉大事'}><NavLink to={'ControlArticle'}>天汉大事</NavLink></Menu.Item>
                    <Menu.Item onClick={()=>ChangeType('两汉三国')} key={'两汉三国'}><NavLink to={'ControlArticle'}>两汉三国</NavLink></Menu.Item>
                    <Menu.Item onClick={()=>ChangeType('汉中旅游')} key={'汉中旅游'}><NavLink to={'ControlTravel'}>汉中旅游</NavLink></Menu.Item>
                    <Menu.Item onClick={()=>ChangeType('魅力汉中')} key={'魅力汉中'}><NavLink to={'ControlArticle'}>魅力汉中</NavLink></Menu.Item>
                    <Menu.Item onClick={()=>ChangeType('汉中美食')} key={'汉中美食'}><NavLink to={'ControlArticle'}>汉中美食</NavLink></Menu.Item>
                    <Menu.Item onClick={()=>ChangeType('汉中特产')} key={'汉中特产'}><NavLink to={'Controltrade'}>汉中特产</NavLink></Menu.Item>
                </Menu>;

            </Sider>
            <Layout
                className="site-layout"
                style={{
                    marginLeft: 200,
                }}
            >
                <Content
                    style={{
                        margin: '24px 16px 0',
                        overflow: 'initial',
                    }}
                >
               <Routes>
               <Route path='writeArticle' element={<WriteArticle />}></Route>
               <Route path='ControlArticle' element={<ArticleList key={controlType} Url={href} ListType={controlType}/>}></Route>
               <Route path='Controltrade' element={<Trade/>}></Route>
               <Route path='ControlTravel/*' element={<ControlTravel/>}></Route>
               <Route path='*' element={<WriteArticle />}></Route>
               </Routes>
                </Content>
            </Layout>
        </Layout>
)
                };

export default Manager;