import React, { useEffect , useRef, useState} from "react";
import axios from "axios";
import { v4 as uuidv4 } from 'uuid';
import { Empty , Button , Image, Input, InputRef } from "antd";
import { useLocation } from "react-router";
import TravelNetWork from "../../travelnetwork";
import './index.css';


const Travel:React.FC = ()=>{
    const Location:any = useLocation();
    const {coverhref , name , rates , starlevel , uuid} = Location.state;
    const href = new TravelNetWork();
    const [Email,setEmail] = useState<string>('');
    const [Phone,setPhone] = useState<string>('');
    const [imagesUrl,setImagesUrl] = useState<string[]>([]);
    const [Hours,setHours] = useState<string>('');
    const [txt,setTxt] = useState<string>('');
    const [isShow,setShow] = useState<boolean>(false);
    const [TicketNumber,setTicketNumber] = useState<number>(1);

   const userEmail = useRef<InputRef>(null);
   const userPhone = useRef<InputRef>(null);

    useEffect(()=>{
     axios({
        method:'GET',
        url:href.getMessage,
        params:{
            uuid
        }
     }).then((res)=>{
        const {bussinessemail,bussinessphone,imagesurl,openhours,text} = res.data[0];
        setEmail(bussinessemail);
        setPhone(bussinessphone);
        setTxt(text);
        setHours(openhours);
        let temp = imagesurl.split(',');
        setImagesUrl(temp);
     }).catch((err)=>{
        throw err;
     }).finally(()=>{

     })
    },[])

    const submit = ()=>{
      const formdata = new FormData();
      let id = uuidv4();
      formdata.append('orderId',id);
      formdata.append('name',String(name + '景区门票'));
      formdata.append('totalPrice',String(TicketNumber * rates));
      formdata.append('message',String(name + '景区门票 * '+TicketNumber+'张'));
     
     sessionStorage.setItem('orderId',id);
     sessionStorage.setItem('href','http://localhost:8080/pay/paySuccess')
     sessionStorage.setItem('buyerEmail',String(userEmail.current?.input?.value));
     sessionStorage.setItem('buyerPhone',String(userPhone.current?.input?.value));
     sessionStorage.setItem('message',String(name + '景区门票 * '+TicketNumber+'张'))
     sessionStorage.setItem('bussinessemail',Email);
     sessionStorage.setItem('bussinessphone',Phone);
      axios({
        url:href.payMent,
        method:'POST',
        headers: {
          dataType: "JSONP",
          "Content-type": "application/x-www/form",
        },
        data:formdata
      }).then((res:any)=>{
          window.location.href = res.data.result;
      }).catch((err)=>{
        throw err;
      }).finally(()=>{
        
      })
    }
   
    const ChangeShow = ()=>{
       setShow(!isShow);
    }

    return(
        <div className="Page-Travel">
         <div className="Page-Main">
           <div className="Page-Message">
             <div className="CoverShow"> 
                <img src={coverhref} style={{
                    display:'block',
                    width:'400px',
                    height:'500px'
                }}></img>
             </div>
             <div className="Message-Show">
               <span className="Show-Name">景点名: {name} --- 国家 {starlevel} 级景区</span>
               <span style={{
                fontSize:'18px',
                marginTop:'20px'
               }}>简介: {txt}</span>
               <span className="Open-Hours">开放时间: {Hours}</span>
               <span className="Rates">票价: ￥{rates}</span>
               <div style={{
                marginTop:'20px'
               }}>
                 <span className="Manger-Phone"></span>
                 <span style={{
                    float:'left',
                    marginRight:'30px'
                 }}>{Phone}</span>
                 <span className="Manger-QQ"></span>
                 <span>{Email}</span><br/>
                 <Button onClick={()=>ChangeShow()} style={{
                   marginTop:'80px',
                 }}>门票预订</Button>
               </div>
             </div>
           </div>
         </div>
         <div className="Notice">
            <span>园区公告:</span>
            <br />
            <span style={{
                display:'block',
                marginTop:'20px'
            }}>防疫政策:</span>
            <p style={{
                marginTop:'10px'
            }}>
1、陕西省内游客 查验行程卡绿码、健康码绿码、测温正常即可入园 2、陕西省外其他地区游客 查验48小时核酸检测阴性证明、查验行程卡绿码、健康码绿码、测温正常即可入园 3、中高风险区域游客或健康码、行程码异常人员暂无法接待，敬请谅解！ 4、景区实行分时预约，分为：8:00-12:00；12:00-15:30两个场次，请您根据自己的出游时间选购相应的场次。 5、暑期客流量较大，所有游客请提前网上预约购票成功以后出游。游览全程需佩戴口罩。</p>
         </div>
         
         <div className="Travel-Images">
         <span style={{
            display:'block',
            fontSize:'20px',
            fontWeight:'700',
            marginTop:'100px',
            marginBottom:'20px',
         }}>景区一角</span>
           {
            imagesUrl.length == 0 ? <Empty/> : imagesUrl.map((item,index)=>{
                return <Image src={item} key={item} style={{
                    width:'500px',
                    height:'500px',
                    marginTop:'20px'
                }}/>
            })
           }
         </div>
         <div className="BookTicket" style={{
          display: isShow ? 'block' : 'none'
         }}>
              <div className="User_cancel" onClick={()=>ChangeShow()}></div>
             <div className="TicketMessage">订单名: {name} 景区门票</div>
             <div className="TicketMessage">手机号:<Input ref={userPhone} style={{
              width:'70%'
             }}/></div>
             <div className="TicketMessage">邮箱:<Input ref={userEmail} style={{
              width:'70%'
             }}/></div>
             <div className="TicketMessage">数量: <button  disabled={ TicketNumber <=1 ? true : false}  className="TicketNumber" onClick={()=>setTicketNumber(TicketNumber - 1)}>➖</button> {TicketNumber} <button onClick={()=>setTicketNumber(TicketNumber + 1)}  className="TicketNumber">➕</button></div>
             <div className="TicketMessage">总价: ￥ {TicketNumber * rates} (元)</div>
             <div className="TicketMessage"><Button onClick={()=>submit()}>点击支付</Button></div>
         </div>
        </div>
    )
}

export default Travel;