import React , {useState , useEffect} from "react";
import { NavLink , Routes , Route , Navigate } from "react-router-dom";
import OpenTravel from "./open";
import AddTravel from "./add";
import './index.css';

const ControlTravle:React.FC = ()=>{

        

    return(
        <div className="ControlTrade">
           <div className="TradeRoute">
            <NavLink to={'open'} style={{
                fontSize:'17px'
            }} >开放景区</NavLink>
            <NavLink to={'add'} style={{
                fontSize:'17px'
            }}>添加景区</NavLink>
           </div>
           <div className="TradeView">
             <Routes>
                <Route path="open" element={<OpenTravel/>}></Route>
                <Route path="add" element={<AddTravel/>}></Route>
                <Route path="*" element={<OpenTravel/>}></Route>
             </Routes>
           </div>
        </div>
    )
}

export default ControlTravle;