import React, { useEffect, useState } from "react";
import axios from "axios";
import { NavLink } from "react-router-dom";
import Banner from "../components/banner";


const Home = () => {
    const [imagesUrl, setImagesUrl] = useState([]);
    const [introduction, setIntroduction] = useState<string>('');
    useEffect(() => {
        let arr: string[] = []
        axios({
            url: 'http://localhost:8080/getImagesUrl',
            method: 'GET'
        }).then((res) => {
            res.data.forEach((element: any) => {
                arr.push(element.url);
            });
        }).catch((err) => {
            throw err;
        }).finally(() => {
            setImagesUrl(arr as []);
        })
    }, imagesUrl)


    useEffect(() => {
        axios({
            method: 'GET',
            url: 'http://localhost:8080/get_introduction'
        }).then((res) => {
            setIntroduction(res.data);
        }).catch((err) => {
            throw err;
        })
    }, [])

    return(
        <div className="pages">
            <NavLink className={'Manager'} to={'/Manager'}></NavLink>
            <div className="HomeNavigate">
                <a href="http://www.shaanxi.gov.cn/">陕西省人民政府</a>
                <a href="http://www.hanzhong.gov.cn/">汉中市人民政府</a>
                <a href="http://rd.hanzhong.gov.cn/">市人大</a>
                <a href="http://zx.hanzhong.gov.cn/">市政协</a>
            </div>
                <div style={{
                    width:'100%',
                    height:'300px'
                }}>
                <Banner/>
                </div>
                <div className="briefIntroduction">
                    <div className="Title">概述</div>
                    <span>
                        {introduction}
                    </span>
                    <div className="navigation">
                        <div className="navigationItemA">
                            <div className="navigationhref">
                                 <NavLink className={'Ahref'} to={'/tianhandashi'}>天汉大事</NavLink>
                            </div>
                        </div>
                        <div className="navigationItemB">
                            <div className="ItemB" id="Item01">
                            <div className="navigationhref">
                                <NavLink className={'Ahref'} to={'/history'}>两汉三国</NavLink>
                                </div>
                            </div>
                            <div className="ItemB" id="Item02">
                            <div className="navigationhref">
                                <NavLink className={'Ahref'} to={'/travel'}>汉中旅游</NavLink>
                                </div>
                            </div>
                        </div>
                        <div className="navigationItemC">
                            <div className="ItemC" id="ItemcLV">
                                <div className="navigationhref">
                                <NavLink className={'Ahref'} to={'/meilihanzhong'}>魅力汉中</NavLink>
                                </div>
                            </div>
                            <div className="ItemC" id="ItemcMS">
                                <div className="navigationhref">
                                <NavLink className={'Ahref'} to={'/hanzhongmeishi'}>汉中美食</NavLink>
                                </div>
                            </div>
                            <div className="ItemC" id="ItemcTC">
                                <div className="navigationhref">
                                <NavLink className={'Ahref'} to={'/Trade'}>汉中特产</NavLink>
                                </div>
                            </div>
                            <div className="ItemC" id="ItemcIN">
                                <div className="navigationhref">
                                <NavLink className={'Ahref'} to={'/user'}>我在汉中</NavLink>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    )

    
}

export default Home;