import React from "react";
import Travel from "../../components/Travel";
import Banner from "../../components/banner";
const TravelPages = ()=>{
    return(
        <>
       <div style={{
        width:'80%',
        margin:'auto'
       }}>
       <Banner/>
       </div>
       <div className="TravelPages">
       <Travel/>
       </div>
        </>
    )
}

export default TravelPages;