import React , {useState , useEffect, useRef} from "react";
import { Upload , Button , Input} from "antd";
import axios from "axios";
import { v4 as uuidv4 } from 'uuid';
import {  PlusOutlined } from '@ant-design/icons';
import netWorks from "../../network";
import './WriteArticle.css';

const WriteArticle:React.FC = ()=>{

    const text = useRef<HTMLTextAreaElement>(null);
    const articleType = useRef<HTMLSelectElement>(null)
    const [fileList, setFileList] = useState<any>([]);
    const [Cover,setCover] = useState<any>([]);
    const [isShow,setIsShow] = useState<boolean>(false);
    const [Title,setTiele] = useState<string>('');

    const  net = new netWorks();

    const handleChange = (info:any) => {
        setFileList(info.fileList)
    }

    const CoverChange = (info:any)=>{
      setCover(info.fileList);
    }

    const publish = ()=>{

    let textValue= String(text.current?.value);
    let type  = String(articleType.current?.value);


    let formdata = new FormData();
    formdata.append('title',Title);
    formdata.append('text',textValue);
    formdata.append('images',JSON.stringify(fileList));
    formdata.append('cover',JSON.stringify(Cover));
    formdata.append('type',type);
    formdata.append('uuid',uuidv4());


    axios.post(net.managerPublish,formdata,{
        headers: { "content-type": "multipart/form-data" }
    }).then((res)=>{
       alert(res.data.msg);
    }).catch((err)=>{
       throw err;
    }).finally(()=>{

    })

    }

    const uploadButton = (
        <div>
            <PlusOutlined />
            <div style={{ marginTop: 8 }}>Upload</div>
        </div>
    );

    const TitleChange = (e:React.ChangeEvent<HTMLInputElement>) =>{
         setTiele(e.currentTarget.value);
    }
    return(
        <>
           <div  className="WriteArticle">
                  <textarea ref={text}  style={{
                    width:'100%',
                    height:'100%',
                    resize:'none'
                  }} name="text" cols={20} rows={10} placeholder="最多输入200字"></textarea>
           </div>
           <span style={{
                display:'block',
                marginTop:'20px',
                marginBottom:'20px',
                fontSize:'20px',
                fontWeight:'700'
            }}>添加图片</span>
           <div className="UploadImage">
           <Upload
                    multiple={true}
                    listType="picture-card"
                    beforeUpload={() => {
                        return false;
                    }}
                    onChange={(info) => { handleChange(info) }}
                >
                    {fileList.length >= 6 ? null : uploadButton}
                </Upload>
                <Button onClick={()=>setIsShow(true)}>编辑完成</Button>
           </div>
          
           <div className="UploadControl" style={{display:isShow ? 'block' : 'none'}}>
               <div className="Article_cancel" onClick={()=>setIsShow(false)}></div>
               <span
               style={{
                display:'block',
                marginTop:'20px',
                marginBottom:'20px',
                fontSize:'20px',
                fontWeight:'700'
            }}
               >文章标题:</span>
               <Input maxLength={15} placeholder="最多输入15字" onChange={e=>TitleChange(e)}></Input>
               <span
               style={{
                display:'block',
                marginTop:'20px',
                marginBottom:'20px',
                fontSize:'20px',
                fontWeight:'700'
            }}
               >选择封面:</span>
               <Upload
                    multiple={true}
                    listType="picture-card"
                    beforeUpload={() => {
                        return false;
                    }}
                    onChange={(info) => { CoverChange(info) }}
                >
                    {Cover.length >= 1 ? null : uploadButton}
                </Upload>
                <span
               style={{
                display:'block',
                marginBottom:'10px',
                fontSize:'20px',
                fontWeight:'700'
            }}
               >选择类型:</span>
                <select ref={articleType} defaultValue={'天汉大事'}>
                    <option value={'天汉大事'}>天汉大事</option>
                    <option value={'两汉三国'}>两汉三国</option>
                    <option value={'汉中美食'}>汉中美食</option>
                </select> <br/>
                <Button style={{
                    marginBottom:'20px',
                    marginTop:'20px',
                }} onClick={()=>publish()}>点击发表</Button>
           </div>
        </>
    )
}

export default WriteArticle;