import React from "react";
import {  Button} from "antd";
import { TypeListItem } from "../../publicType";
const ListItem:React.FC<TypeListItem> = (props:TypeListItem)=>{

    const {id,title,coverhref,uuid,type} = props.message; 
  
    

    return(
        <> 
         <li className="articleItem">
            <img src={coverhref} style={{width:'100px',height:'100px'}} alt="" />
            <span className="articleTitle">{title}</span>
            <span className="articleType">类型:{type}</span>
             <Button style={{display:props.Control?'block' : 'none'}}>点击删除</Button>
         </li>
        </>
    )
}

export default ListItem;