import axios from "axios";
import React , {useState , useEffect} from "react";
import { Empty } from "antd";
import tradeNetWork from '../../Tradenetwork';
import TradeItem from "./TradesItem";
import './index.css';
interface Iprops{
    isShow:boolean
}
const Trades:React.FC<Iprops> = (props:Iprops)=>{
    const {isShow} = props;
    const [messageList,setMessageList] = useState([])
    const href = new tradeNetWork();
    useEffect(()=>{
        axios({
            method:'GET',
            url:href.getList
        }).then((res)=>{
           if(res.data!=false){
            setMessageList(res.data);
           }
           else{
            alert('当前网络繁忙,请稍后再试!');
           }
        }).finally(()=>{
    
        })
    },[])
    return(
        <div className="ControlTrades" style={{display : isShow? 'block' : 'none'}}>
             {
                 messageList.length == 0 ? <Empty/> : messageList.map((item:any,index)=>{
                    return <TradeItem message={item} key={item.uuid} />
                 })
             }
        </div>
    )
}

export default Trades;