import { Button } from "antd";
import React from "react";

interface Iprops{
   message:{
    telephone:string,
    title:string,
    coverhref:string,
    uuid:string
   }
}

const UserArticleItem:React.FC<Iprops> = (props:Iprops)=>{

    const {telephone,title,coverhref,uuid} = props.message;
    return(
        <>
        <li className="UserArticleItem">
          <img src={coverhref} style={{width:'80px',height:'80px'}}></img>
          <span style={{fontSize:'20px'}}>标题: {title}</span>
          <Button>删除</Button>
        </li>
        </>
    )
}

export default UserArticleItem;