import { Button } from "antd";
import React, { useEffect } from "react";
import { useNavigate } from "react-router";
import './index.css';
interface Iprops{
    message:{
        uuid:string,
        tradename:string,
        tradeprice:string,
        coverhref:string
    }
}
const TradeItem:React.FC<Iprops> = (props:Iprops)=>{
    const navigate = useNavigate();
    const {uuid,tradename,tradeprice,coverhref} = props.message;
    const goto = ()=>{
      navigate('/TradePages',{state:props.message,replace:true});
    }
    return(
        <li className='TradeItem' onClick={()=>goto()}>
            <img src={coverhref} style={{width:'100px',height:'100px'}}></img>
            <span style={{fontSize:'15px',fontWeight:700,marginTop:'15px'}}>{tradename}</span>
            <span style={{fontSize:'18px',fontWeight:700,marginTop:'10px'}}>{tradeprice}</span>
            <Button danger={true}>下架该产品</Button>
        </li>
    )
}

export default TradeItem;