import axios from "axios";
import React , {useState , useEffect} from "react";
import { Empty } from "antd";
import tradeNetWork from '../../Tradenetwork';
import Item from "./Item";

const List:React.FC =()=>{
    const [messageList,setMessageList] = useState([])
    const href = new tradeNetWork();
    useEffect(()=>{
        axios({
            method:'GET',
            url:href.getList
        }).then((res)=>{
           if(res.data!=false){
            setMessageList(res.data);
           }
           else{
            alert('当前网络繁忙,请稍后再试!');
           }
        }).finally(()=>{
    
        })
    },[])
    return(
        <div className="ControlTrades" >
             {
                 messageList.length == 0 ? <Empty/> : messageList.map((item:any,index)=>{
                    return <Item message={item} key={item.uuid} />
                 })
             }
        </div>
    )
}

export default List;