import React , {useEffect , useState ,useRef}from "react";
import axios from "axios";
import { v4 as uuidv4 } from 'uuid';
import { Empty , Image , Button , Input, InputRef} from "antd";
import { useLocation } from "react-router";
import tradeNetWork from "../../Tradenetwork";
import './index.css';
const TradePage:React.FC = ()=>{
    const Location:any = useLocation();
    const {coverhref,tradename,tradeprice,uuid} = Location.state;
    console.log(Location.state);
    const [Email,setEmail] = useState<string>('');
    const [phone,setPhone] = useState<string>('');
    const [imagesUrl,setImagesUrl] = useState<string[]>([]);
    const [text,setText] = useState<string>('');
    const [isShow,setIshow] = useState<boolean>(false);
    const [TradesNumber,setTradesNumber] = useState<number>(1);

    const userEmail = useRef<InputRef>(null);
   const userPhone = useRef<InputRef>(null);
   const userAddress = useRef<InputRef>(null);
   const userName = useRef<InputRef>(null);

    const href = new tradeNetWork();
    useEffect(()=>{
      axios({
        method:'GET',
        url:href.getMessage,
        params:{
            uuid
        }
      }).then((res)=>{
        const {bussinessemail,bussinessphone , tradeimages , tradeintroduction} = res.data[0];
        setEmail(bussinessemail);
        setPhone(bussinessphone);
        setText(tradeintroduction);
        let temp = tradeimages.split(',');
        setImagesUrl(temp);
    }).catch((err)=>{
        throw err;
      }).finally(()=>{

      })
    },[]);

    const submit=()=>{
        const formdata = new FormData();
      let id = uuidv4();
      formdata.append('orderId',id);
      formdata.append('name',String(tradename));
      formdata.append('totalPrice',String(TradesNumber * tradeprice));
      formdata.append('message',String(tradename+' *'+TradesNumber+' 个'+',配送至'+String(userAddress.current?.input?.value))+', 收件人:'+String(userName.current?.input?.value));
     
       

     sessionStorage.setItem('orderId',id);
     sessionStorage.setItem('href','http://localhost:8080/pay/buySuccess')
     sessionStorage.setItem('buyerEmail',String(userEmail.current?.input?.value));
     sessionStorage.setItem('buyerPhone',String(userPhone.current?.input?.value));
     sessionStorage.setItem('message',String(tradename+' *'+TradesNumber+' 个'+',配送至'+String(userAddress.current?.input?.value))+', 收件人:'+String(userName.current?.input?.value));
     sessionStorage.setItem('bussinessemail',Email);
     sessionStorage.setItem('bussinessphone',phone);
      axios({
        url:href.payMent,
        method:'POST',
        headers: {
          dataType: "JSONP",
          "Content-type": "application/x-www/form",
        },
        data:formdata
      }).then((res:any)=>{
          window.location.href = res.data.result;
      }).catch((err)=>{
        throw err;
      }).finally(()=>{
        
      })
    }

    return(
        <>
          <div className="PagesTrade">
             <div className="Pages-Main">
                <img src={coverhref} style={{
                    display:'block',
                    width:'400px',
                    height:'500px',
                    float:'left'
                }}></img>
                <div className="Pages-Message">
                    <span style={{
                        fontSize:'22px',
                        fontWeight:'700'
                    }}>商品名: {tradename}</span>
                    <span style={{
                        fontSize:'16px',
                        fontWeight:'500',
                        marginTop:'30px'
                    }}>简介: {text}</span>
                    <span  style={{
                        fontSize:'20px',
                        fontWeight:'500',
                        marginTop:'30px'
                    }}>
                     单价: ￥ {tradeprice}
                    </span>
                   <div style={{
                     marginTop:'50px'
                   }}>
                   <span style={{
                    float:'left',
                    marginRight:'20px'
                   }}>联系商家:</span>
                   <span className="Manger-Phone"></span>
                   <span style={{
                    float:'left',
                     marginRight:'30px'
                   }}>{phone}</span>
                   <span className="Manger-QQ"></span>
                   <span>{Email}</span>
                   </div>
                   <Button style={{
                    marginTop:'50px'
                   }} onClick={()=>setIshow(! isShow)}>点击购买</Button>
                </div>
             </div>
             <div className="Pages-Images">
                <span className="Pages-title">商品参考图:</span>
                {
                    imagesUrl.length == 0 ? <Empty/> : imagesUrl.map((item,index)=>{
                        return <Image src={item} key={index} style={{
                            width:'500px',
                            height:'500px',
                            marginBottom:'10px',
                            marginTop:'10px'
                        }}/>
                    })
                }
             </div>
             <div className="TradeForm" style={{
                display: isShow ? 'block' : 'none'
             }}>
                <div className="User_cancel" onClick={()=>setIshow(! isShow)}></div>
                <div className="FormItem">商品名: {tradename}</div>
                <div className="FormItem"><span>配送至:</span><Input ref={userAddress} style={{width:'70%'}}/></div>
                <div className="FormItem"><span>手机号:</span><Input ref={userPhone} style={{width:'70%'}}/></div>
                <div className="FormItem"><span>QQ邮箱:</span><Input ref={userEmail} style={{width:'70%'}}/></div>
                <div className="FormItem"><span>收件人:</span><Input ref={userName} style={{width:'70%'}}/></div>
                <div className="FormItem"><div className="TicketMessage">数量: <button  disabled={ TradesNumber <=1 ? true : false}  className="TicketNumber" onClick={()=>setTradesNumber(TradesNumber - 1)}>➖</button> {TradesNumber} <button onClick={()=>setTradesNumber(TradesNumber + 1)}  className="TicketNumber">➕</button></div></div>
                <div className="FormItem">总价: ￥:{TradesNumber * tradeprice} (元)</div>
                <div className="FormItem"><Button onClick={()=>submit()}>提交订单</Button></div>
             </div>
          </div>
        </>
    )
}

export default TradePage;