import React, { useEffect, useState } from "react";
import { Empty } from "antd";
import axios from "axios";
import PublicItem from "./publicItem";
import './index.css';
interface Iprops{
    href:string
}
const PublicArticleList:React.FC<Iprops> = (props:Iprops)=>{

    const [messageList,setMessageList] = useState([]);

    useEffect(()=>{
        axios({
            method:'GET',
            url:props.href
          }).then((res)=>{
            console.log(res.data);
            setMessageList(res.data)
          }).catch((err)=>{
             throw err;
          }).finally(()=>{
              
          })
    },[0])

    return(
        <>
         <ul className="publicList">
            {
                messageList.length == 0 ? <Empty/> : messageList.map((item:any,index)=>{
                    return <PublicItem message={item} key={item.uuid}/>
                })
            }
         </ul>
        </>
    )
}

export default PublicArticleList;