import React, { useEffect } from "react";
import { useLocation } from "react-router";
import axios from "axios";
import TravelNetWork from "../../travelnetwork";
const PaySuccess:React.FC =()=>{
    const href = new TravelNetWork()
    useEffect(()=>{
    const orderId = sessionStorage.getItem('orderId');
    const buyerEmail = sessionStorage.getItem('buyerEmail');
    const buyerPhone = sessionStorage.getItem('buyerPhone');
    const message = sessionStorage.getItem('message');
    const bussinessemail = sessionStorage.getItem('bussinessemail');
    const bussinessphone = sessionStorage.getItem('bussinessphone');
    const href:string = String(sessionStorage.getItem('href'));
    axios({
        method:'GET',
        url:href,
        params:{
            orderId,
            buyerEmail,
            buyerPhone,
            message,
            bussinessemail,
            bussinessphone
        }
    }).then((res)=>{
        
    }).catch((err)=>{
         throw err;
    }).finally(()=>{

    })
    })

    return(
        <>
        支付成功!
        </>
    )
}

export default PaySuccess;