import React from "react";
import Banner from "../../components/banner";
import List from "./List";
const HZTC = ()=>{
    return(
        <>
       <div style={{
        width:'80%',
        margin:'auto'
       }}>
       <Banner/>
       <span style={{
        display:'block',
        marginTop:'20px',
        marginBottom:'20px',
        fontSize:'22px',
        fontWeight:'700'
       }}>汉中特产</span>
       <List/>
       </div>
        </>
    )
}

export default HZTC;