import React, { useEffect, useState } from "react";
import axios from "axios";
import { Empty } from "antd";
import Item from "./Item";
interface Iprops{
   href:string
}

const UserArticleList:React.FC<Iprops> = (props:Iprops)=>{
    const {href} = props;
    const [messageList,setMessageList] = useState([])

    useEffect(()=>{
        axios({
            method:'GET',
            url:href,
        }).then((res)=>{
         console.log(res.data);
         setMessageList(res.data);
        }).catch((err)=>{
    
        }).finally(()=>{
    
        })
    },[])

    

   

    return(
        <>
          <ul className="userArticleList">
             {
                
                messageList.length == 0 ? <Empty/> : messageList.map((item:any,index)=>{
                    return <Item key={item.uuid} message={item}/>
                 })
             }
          </ul>

        </>
    )
}

export default UserArticleList;