import { List , Empty } from 'antd';
import axios from 'axios';
import React , {useState , useEffect} from 'react';
import {Article_List} from '../../publicType'
import ListItem from './ListItem';
import { TypeListItem } from "../../publicType";
import './List.css';
const ArticleList:React.FC<Article_List> = (props:Article_List)=>{
   const [messageList,setMessageList] = useState([]);
   const ListType = props.ListType;
   
   useEffect(()=>{
    axios({
        method:'get',
        url:props.Url,
        params:{
            'type':props.ListType
        }
       }).then((res)=>{
          console.log('发了网络请求');
          console.log(props.ListType);
          console.log(res.data);
          setMessageList(res.data);
       }).catch((err)=>{
          throw err;
       }).finally(()=>{
    
       })
   },[])
    
   

    return(
        <>
        <ul className='articlelist'>
               {
               messageList.length==0? <Empty/>  : messageList.map((item:any,index)=>{
                     return <ListItem message={item} Control={true} key={item.uuid}/>
                })
               }
        </ul>
        </>
    )
}

export default ArticleList;