import React , {useRef, useState} from "react";
import axios from 'axios';
import { Button, Input , InputRef, Upload} from "antd";
import { v4 as uuidv4 } from 'uuid';
import {  PlusOutlined } from '@ant-design/icons';
import netWorks from "../../network";
import './user.css';

interface Iprops{
    isShow:boolean,
    changeShow:Function,
    telephone:string,
    changeListId:Function
}
const UserWrite:React.FC<Iprops> = (props:Iprops)=>{
 
    const {isShow} = props;
    const [fileList, setFileList] = useState<any>([]);
    const [Cover,setCover] = useState<any>([]);
    const title = useRef<InputRef>(null);
    const text = useRef<HTMLTextAreaElement>(null);

    const handleChange = (info:any) => {
        setFileList(info.fileList)
    }

    const CoverChange = (info:any)=>{
        setCover(info.fileList);
      }

    const uploadButton = (
        <div>
            <PlusOutlined />
            <div style={{ marginTop: 8 }}>Upload</div>
        </div>
    );

    const Submit=()=>{
    //    console.log(title.current?.input?.value);
    //    console.log(text.current?.value);
       let txtTiele = String(title.current?.input?.value);
       let textText = String(text.current?.value);
       let network = new netWorks();

        let formdata = new FormData();
        formdata.append('title',txtTiele);
        formdata.append('text',textText);
        formdata.append('images',JSON.stringify(fileList));
        formdata.append('cover',JSON.stringify(Cover));
        formdata.append('uuid',uuidv4());
        formdata.append('Telephone',props.telephone);

        axios.post(network.userWrite,formdata,{
            headers: { "content-type": "multipart/form-data" }
        }).then((res)=>{
            console.log(res.data);
        }).catch((err)=>{
            throw err;
        }).finally(()=>{
            props.changeShow();
        })
    }

    return(
        <>
        <div className="UserWrite" style={{display:isShow? 'block' : 'none'}}> 
        <div className="User_cancel" onClick={()=>props.changeShow()} ></div>
        <span style={{fontSize:'20px'}}>文章标题:</span>
        <Input ref={title} style={{width:'30%',marginLeft:'20px'}}  placeholder={'最多输入15字'}/><br/>
        <span style={{fontSize:'20px'}}>文章封面:</span>
        <Upload
                    multiple={true}
                    listType="picture-card"
                    beforeUpload={() => {
                        return false;
                    }}
                    onChange={(info) => { CoverChange(info) }}
                >
                    {Cover.length >= 1 ? null : uploadButton}
                </Upload>
        <span style={{fontSize:'20px',marginBottom:'30px'}}>文章内容:</span><br></br>
        <textarea ref={text} name="user_text" id="user_text" cols={30} rows={10}
        style={{
         width:'100%',
         height:'200px',
         resize:'none',
        }}
        placeholder='最多输入100字'
        ></textarea>
        <span style={{fontSize:'20px'}}>文章图片:</span>
        <Upload
                    multiple={true}
                    listType="picture-card"
                    beforeUpload={() => {
                        return false;
                    }}
                    onChange={(info) => { handleChange(info) }}
                >
                    {fileList.length >= 6 ? null : uploadButton}
                </Upload>
        <Button style={{
             position: 'absolute',
             bottom:'2%',
             left:'50%',
             marginLeft:'-44px'
        }} onClick={()=>Submit()}>点击上传</Button>
        </div>
        </>
    )
}

export default UserWrite;