import React , {useEffect , useState} from "react";
import axios from "axios";
import { Empty } from "antd";
import Item from './Item';
import TravelNetWork from "../../travelnetwork";
import './index.css';
const Travel:React.FC = ()=>{

    const [messageList,setMessageList] = useState([])
    const href = new TravelNetWork();
    console.log(href.getList)
    useEffect(()=>{
        axios({
            method:'GET',
            url:href.getList
        }).then((res)=>{
           if(res.data.length!=0){
            setMessageList(res.data);
           }
        }).finally(()=>{
    
        })
    },[])

    return(
        <>
        <span style={{display:'block',marginTop:'20px',marginBottom:'20px' ,fontSize:'22px',fontWeight:'700'}}>开放景区</span>
         <div className="Trades">
             {
                 messageList.length == 0 ? <Empty/> : messageList.map((item:any,index)=>{
                    return <Item key={item.uuid} message={item}/>
                 })
             }
        </div>
        </>
    )
}

export default Travel;