import React , {useEffect , useState} from "react";
import axios from "axios";
import { Empty } from "antd";
import OpenItem from "./openItem";
import TravelNetWork from "../../travelnetwork";
const OpenTravel:React.FC = ()=>{

    const [messageList,setMessageList] = useState([])
    const href = new TravelNetWork();
    console.log(href.getList)
    useEffect(()=>{
        axios({
            method:'GET',
            url:href.getList
        }).then((res)=>{
           if(res.data.length!=0){
            setMessageList(res.data);
           }
        }).finally(()=>{
    
        })
    },[])

    return(
        <>
         <div className="ControlTrades">
             {
                 messageList.length == 0 ? <Empty/> : messageList.map((item:any,index)=>{
                    return <OpenItem key={item.uuid} message={item}/>
                 })
             }
        </div>
        </>
    )
}

export default OpenTravel;