import React from "react";
import { useNavigate } from 'react-router-dom';
interface Iprops{
    message:{
        id:number,
        title:string,
        coverhref:string,
        uuid:string,
        type:string
    }
}

const PublicItem:React.FC<Iprops> =(props:Iprops)=>{
    const navigate = useNavigate();
    const {id,title,coverhref,uuid,type} = props.message;
     
    const goto = ()=>{
        navigate('/article',{state:props.message,replace:true})
    }

    return (
        <>
        <li className="publicitem" onClick={()=>goto()}>
            <img src={coverhref} style={{width:'100px',height:'100px'}}></img>
            <span >标题:{title}</span>
        </li>
        </>
    )
}

export default PublicItem;