import React , {useState , useEffect, HtmlHTMLAttributes, ReactNode} from "react";
import { Input , Button} from "antd";
import './Login.css';
interface Iprops{
   IsShow:boolean,
   setShow:Function,
   FLogin:Function,
   FRegister:Function
}

const Istyle:React.CSSProperties={
    marginBottom:'10px'
}

const Border:React.CSSProperties={
    borderBottom:'1px solid blue',
    cursor:'pointer'
}

const Login: React.FC<Iprops> = (props:Iprops)=>{
    const {IsShow} = props;
    const {setShow} = props;
    const {FLogin} = props;
    const {FRegister} = props;
    const [DiffShow,setDiffShow] = useState<string>('登录');
    const [TelephoneA,setTelephoneA] = useState<string>('');
    const [TelephoneB,setTelephoneB] = useState<string>('');
    const [Email,setEmail] = useState<string>('');

    const ChangeTelephone = (e:React.ChangeEvent<HTMLInputElement>):void =>{
        if(DiffShow == '登录'){
            setTelephoneA(e.currentTarget.value);
        }
        setTelephoneB(e.currentTarget.value);
    }

    const ChangeEmail = (e:React.ChangeEvent<HTMLInputElement>):void =>{
        setEmail(e.currentTarget.value);
    }

    return(
        <>  
        <div className="Login" style={{display:IsShow ? 'block' :'none'}}>
            <span className="User_cancel" onClick={()=>setShow()}></span>
            <div style={{display:DiffShow == '登录' ? 'block' : 'none'}}>
            <div style={Istyle}>
                <span >手机号:</span><Input onChange={ChangeTelephone}></Input>
            </div>
            <div style={{marginTop:'20px'}}>
                <Button onClick={()=>{FLogin(TelephoneA)}}>点击登录</Button>
            </div>
            </div>
            <div style={{display:DiffShow == '注册' ? 'block' : 'none'}}>
            <div style={Istyle}>
                <span >邮箱:</span><Input onChange={ChangeEmail}></Input>
            </div>
            <div style={Istyle}>
                <span >手机号:</span><Input onChange={ChangeTelephone}></Input>
            </div>
            <div style={{marginTop:'20px'}}>
                <Button onClick={()=>{FRegister(TelephoneB,Email)}}>点击注册</Button>
            </div>
            </div>
            <div className="DiffShow">
                <span onClick={()=>{setDiffShow('登录')}} style={DiffShow == '登录' ? Border : {cursor:'pointer'}}>登录</span>
                <span onClick={()=>{setDiffShow('注册')}} style={DiffShow == '注册' ? Border : {cursor:'pointer'}}>注册</span>
            </div>
        </div>
        </>
    )
}

export default Login;