import { Button, Input } from "antd";
import React, { useState } from "react";
import { TypeAmend } from "../../publicType";

const Amend:React.FC<TypeAmend> = (props:TypeAmend)=>{
    const {isShow} = props;
    const [UserName,setUserName] = useState<string>(props.UserName);
    const [Email,setEmail] = useState<string>(props.Email);
    const [Address,setAdress] = useState<string>(props.Address);
 
    const changeName=(e:React.ChangeEvent<HTMLInputElement>)=>{
        setUserName(e.currentTarget.value);
    }

    const changeEmail=(e:React.ChangeEvent<HTMLInputElement>)=>{
        setEmail(e.currentTarget.value);
    }

    const changeAddres=(e:React.ChangeEvent<HTMLInputElement>)=>{
        setAdress(e.currentTarget.value);
    }

    return(
        <>
            <div className="UserAmend" style={{display:isShow? 'block' : 'none'}}>
              <div className="User_cancel" onClick={()=>props.changeShow()}></div>
              <div className="AmendItem">
                <span style={{fontSize:'17px',width:'50px'}}>昵称:</span><Input onChange={(e)=>changeName(e)} placeholder={UserName}/>
              </div>
              <div className="AmendItem">
                <span style={{fontSize:'17px',width:'50px'}}>邮箱:</span><Input onChange={(e)=>changeEmail(e)} placeholder={Email}/>
              </div>
              <div className="AmendItem">
                <span style={{fontSize:'17px',width:'50px'}}>地址:</span><Input onChange={(e)=>changeAddres(e)}  placeholder={Address}/>
              </div>
              <div className="AmendItem" style={{marginTop:'50px'}}>
                 <Button  onClick={()=>props.submit(UserName,Email,Address)}>点击提交</Button>
              </div>
            </div>
        </>
    )
}

export default Amend;