import React from "react";
import List from './List';
import Publicnetwork from "../../Publicnetwork";
const MLhanzhong = ()=>{
    const network = new Publicnetwork();

    return(
        <>
        <div className="pages">
             <div className="pagesmessage">
               <List href={network.getUserArticle}/>
             </div>
          </div>
        </>
    )
}

export default MLhanzhong;