import React from "react";
import {Button} from 'antd';
interface Iprops{
    message:{
        uuid:string,
        name:string,
        starlevel:string,
        rates:string,
        coverhref:string
    }
}

const OpenItem:React.FC<Iprops> = (props:Iprops)=>{
    const {uuid,name,starlevel,rates,coverhref} = props.message;
    return(
        <>
        <li className='TradeItem'>
            <img src={coverhref} style={{width:'100px',height:'100px'}}></img>
            <span style={{fontSize:'15px',fontWeight:700,marginTop:'15px'}}>{name}</span>
            <span style={{fontSize:'18px',fontWeight:700,marginTop:'10px'}}>门票: ￥{rates}</span>
            <span style={{fontSize:'18px',fontWeight:700,marginTop:'10px'}}>{starlevel}级景区</span>
            <Button danger={true} style={{marginTop:'10px'}}>关闭该景区</Button>
        </li>
        </>
    )
}

export default OpenItem;