import React from "react";
import {useNavigate} from 'react-router-dom';
interface Iprops{
    message:{
        uuid:string,
        name:string,
        starlevel:string,
        rates:string,
        coverhref:string
    }
}

const Item:React.FC<Iprops> = (props:Iprops)=>{
    const navigate = useNavigate();
    const {uuid,name,starlevel,rates,coverhref} = props.message;
    const goto = ()=>{
      navigate('/PageTravel',{state:props.message,replace:true});
    }
    return(
        <>
        <li className='TradeItem' onClick={()=>goto()}>
            <img src={coverhref} style={{width:'100px',height:'100px'}}></img>
            <span style={{fontSize:'15px',fontWeight:700,marginTop:'15px'}}>{name}</span>
            <span style={{fontSize:'18px',fontWeight:700,marginTop:'10px'}}>门票: ￥{rates}</span>
            <span style={{fontSize:'18px',fontWeight:700,marginTop:'10px'}}>{starlevel}级景区</span>
        </li>
        </>
    )
}

export default Item;